import * as React from 'react';
import { Button } from 'react-bootstrap';
import { NavLink } from 'react-router-dom';

import { BrightcoveAccount } from 'services';
import { BrightcoveApiGateway } from '@brightcove/video-cloud-sdk';
import { SharePointApiGateway, SPEmbedUtil } from '../../services/sharepoint';
import { ConnectorContext, UserRole, ConnectorValidationResult } from '../../services/connector';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { IconName } from '@fortawesome/fontawesome';
import { Spinner } from '../common';
import autobind from 'autobind-decorator';
import { faDownload } from '@fortawesome/fontawesome-free-solid';
import * as FileSaver from 'file-saver';

import './AccountManagement.css';

export class AccountManagement extends React.Component<AccountManagementProps, AccountManagementState> {
    constructor(props: AccountManagementProps, context?: any) {
        super(props, context);

        this.state = {
            isAddingAccount: false,
            isLoadingAccounts: true,
            showConfigureProxy: false,
        };
    }

    async componentDidMount() {

        const { connectorContext, spApiGateway, validationResult } = this.props;
        if (validationResult.accountEmptyGroups.length === 0 && (!connectorContext.currentUserRole || connectorContext.currentUserRole !== UserRole.Author)) {
            window.location.hash = '#/';
        }

        // Get the brightcove accounts stored in the host web.
        const accountsList = spApiGateway.accountsList;
        const accounts = await accountsList.getItems();

        this.setState({
            isLoadingAccounts: false,
            accounts
        });
    }

    @autobind
    async showConfigureProxy() {
        this.setState({
            showConfigureProxy: true
        });
    }

    @autobind
    async hideConfigureProxy() {

        this.setState({
            showConfigureProxy: false
        });
    }

    @autobind
    async addNewAccount() {

        this.setState({
            isAddingAccount: true
        });

        const spApiGateway = this.props.spApiGateway;

        const accountsList = spApiGateway.accountsList;
        let newAccount = await accountsList.createNewItem();
        window.location.hash = `#/account-management/view/${newAccount.id}`;
    }

    @autobind
    async downloadEmbedHelper() {
        const { connectorContext } = this.props;
        const embedHelperBlob = SPEmbedUtil.generateEmbedHelper(connectorContext.spAddinContext.SPHostUrl, connectorContext.spAddinContext.SPAppWebUrl);
        FileSaver.saveAs(embedHelperBlob, SPEmbedUtil.getEmbedHelperFileNameFromAppWebUrl(connectorContext.spAddinContext.SPAppWebUrl));
    }

    render() {
        const plusIconName: IconName = 'plus-circle';
        const cogIconName: IconName = 'cog';

        const { isAddingAccount, isLoadingAccounts, accounts } = this.state;

        if (isAddingAccount) {
            return (
                <Spinner loadingText="Creating New Account..." />
            );
        }

        if (isLoadingAccounts || !accounts) {
            return (
                <Spinner loadingText="Loading Accounts..." />
            );
        }

        return (
            <>
                <ol className="breadcrumb">
                    <li><NavLink to="/">Brightcove Management</NavLink></li>
                    <li className="active">Account Management</li>
                </ol>
                {!this.state.isLoadingAccounts &&
                    <div>
                        <table className="table table-striped">
                            <thead>
                                <tr>
                                    <td>Account Title</td>
                                    <td>Authors Group</td>
                                    <td>Viewers Group</td>
                                </tr>
                            </thead>
                            <tbody>
                                {accounts.map((account, i) =>
                                    <tr key={i}>
                                        <td>
                                            <NavLink className="mLinks btn btn-lg btn-primary" to={`/account-management/view/${account.id}`}>
                                                {account.title}
                                            </NavLink>
                                        </td>
                                        <td>
                                            <span>{account.accountAuthorsGroupName}</span>
                                        </td>
                                        <td>
                                            <span>{account.accountViewersGroupName}</span>
                                        </td>
                                    </tr>
                                )}
                            </tbody>
                        </table>
                        <hr />
                        <div>
                            <Button bsSize="large" bsStyle="default" style={{ margin: '2px' }} onClick={this.showConfigureProxy}>
                                <FontAwesomeIcon icon={cogIconName} />&nbsp;Connector Settings
                            </Button>
                            <Button bsSize="large" bsStyle="default" style={{ margin: '2px' }} onClick={this.addNewAccount}>
                                <FontAwesomeIcon icon={plusIconName} />&nbsp;Add new account
                            </Button>
                            {(process.env.NODE_ENV === 'development' || process.env.NODE_ENV === 'production') &&
                                <Button bsSize="large" bsStyle="default" style={{ margin: '2px' }} onClick={this.downloadEmbedHelper}>
                                    <FontAwesomeIcon icon={faDownload} />&nbsp;Download Embed Helper
                                </Button>
                            }
                            <hr />
                        </div>
                    </div>
                }
            </>
        );
    }
}

interface AccountManagementProps {
    connectorContext: ConnectorContext;
    bcApiGateway: BrightcoveApiGateway;
    spApiGateway: SharePointApiGateway;
    validationResult: ConnectorValidationResult;
}

interface AccountManagementState {
    isAddingAccount: boolean;
    isLoadingAccounts: boolean;
    showConfigureProxy: boolean;
    accounts?: Array<BrightcoveAccount>;
}



